@import url('https://fonts.googleapis.com/css2?family=Oxanium:wght@200;300;400;500;600;700;800&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Oxanium', cursive;
}

#header{
    display: flex;
    width: 100%;
}

#navigation-bar{
    display: flex;
    justify-content: end;
    align-items: center;
    width: 100%;
    margin: 30px 20px 0px 0px;
}

.logo{
    width: 280px;
    margin: 30px 0px 0px 40px;
}

.banner-background{
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

.hamburger-menu{
    color: #fff;
    transform: scale(1.7);
}

#banner-content{
    width: 100%;
    height: 88vh;
    display: flex;
    align-items: center;
    padding: 0px 0px 0px 100px;
    color: #fff;
}

/* Buttons */

.banner-button{
    margin-top: 25px;
}

.btn{
    padding: 12px 40px;
    border: none;
}

.btn:hover{
    cursor: pointer;
}

.highlighted{
    background-image: linear-gradient(to right, #ff6b40, #ff5b4d, #ff4a5c, #ff396b, #ff297c, #ff1b8b, #ff0d9a, #ff03ab, #ff03be, #ff10d3, #ff20e8, #ff30ff);
    color: #fff;
}

.outlined{
    border: 2px solid white;
    background: none;
    padding: 10px 38px;
    color: #fff;
    margin-left: 20px;
}

.gradient-line{
    width: 514px;
    height: 1px;
    background-image: linear-gradient(to right, #ff6b40, #ff5b4d, #ff4a5c, #ff396b, #ff297c, #ff1b8b, #ff0d9a, #ff03ab, #ff03be, #ff10d3, #ff20e8, #ff30ff);
    margin-top: 20px;
}

.main-text{
    font-size: 30px;
    font-weight: 400;
    word-spacing: 2px;
}

.subtext{
    font-weight: 300;
    font-size: 20px;
    word-spacing: 3px;
    letter-spacing: 0.6px;
    margin-top: 15px;
}

.bold{
    font-weight: 600;
}

.blue-gradient{
    font-style: italic;
    font-weight: 600;
    font-size: 23px;
    color: #00FFFF;
}

.green-gradient{
    background: linear-gradient(to bottom, #6efff5, #18ffdb, #00ffb2, #22ff78, #66ff19);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: italic;
    font-weight: 700;
}

.red-gradient{
    background: rgb(255,107,64);
    background: linear-gradient(90deg, rgba(255,107,64,1) 0%, rgba(255,27,139,1) 94%, rgba(255,38,197,1) 100%, rgba(255,48,255,1) 100%);-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: italic;
    font-weight: 700;
}